*
{
  margin: 0;
  padding: 0;
}
.app-input
{
  text-align: center;
  color: blue;
}
h1{
  color: green;
}
.app-input input
{
  font-size: 14px;
  border-radius: 5px;
  border: 1px solid rgb(212, 16, 16);
  outline: none;
  padding: 10px 5px;
  width: 200px;
}
.app-input button
{
  background-color: rgb(193, 19, 228);
  color: white;
  border: none;
  outline: none;
  margin-top: 10px;
  padding: 10px 30px;
  font-size: 16px;
  cursor: pointer;
}
nav
{
  background-color: rgb(160, 29, 138);
  padding: 10px 0;
  display: flex;
  justify-content: center;
  margin-top: 5px;
}
nav ul
{
  display: flex;
  list-style-type: none;
  justify-content: space-between;
  align-items: center;
  width: 40%;
  color: rgb(231, 233, 230);
  font-weight: bold;
}
nav ul li
{
  cursor: pointer;
  font-size: 14px;
  transition: all 0.5s ease-in-out;
}
nav ul li:hover
{
  transform: scale(1.4);
  color: rgb(67, 163, 67);
}
.navActive
{
  color: chartreuse;
  font-size: 30px;
  font-weight: bold;
}
.app-container
{
  background-color: brown;
  font-size: 30px;
  color: white;
}